<template lang="">
    <div>
        <head> 
            <link rel="stylesheet" href="https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@20..48,100..700,0..1,-50..200" />
            <link href="https://cdn.jsdelivr.net/npm/bootstrap-icons@1.5.0/font/bootstrap-icons.css" rel="stylesheet" />
            <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css">
            <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/bootstrap@4.0.0/dist/css/bootstrap.min.css">
        </head>

        <!-- Navbar -->
        <div class="row align-items-center ml-0 stickyNavbar"  id="navbar">
            <div id="navbar1" class="col-lg-8 mt-lg-0 col-sm-12 mt-sm-3">
                <div class="d-flex">
                    <div class="ml-3" id="iconBeranda">
                        <router-link to="/">
                            <span class="material-symbols-outlined" style="color: #3c2a21">
                                home
                              </span>
                        </router-link>
                    </div>
                    
                    <div id="judulWeb" class="m-0">
                        <p class="ml-2">Program Studi Informatika UKDW</p>
                    </div>
                </div>
            </div>

            <div id="navbar2" class="col-lg-4 col-sm-12" >
                <div id="idPengguna">
                    <div class="d-flex align-items-center">
                        <span class="material-symbols-outlined">person_filled</span>
                        <div class="dropdown">
                            <button class="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            Nama Dosen
                            </button>
                            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                <div class="dropdown-item">
                                    <router-link to='/login' class="d-flex align-items-center" id="logoutUser">
                                        <span class="material-symbols-outlined mr-2"> logout </span>
                                        Logout
                                    </router-link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <!-- Content -->
        <div id="content" class="container-fluid px-0">
            <div class="col-12 mt-4">
                <div class="row">
                    <div class="col-10 pr-0">
                        <BreadcrumbModule class="m-0 ml-5 breadCrumb"></BreadcrumbModule>
                    </div>
                    <div class="col-1 pl-5">
                        
                    </div>
                </div>
            </div>
        
            <div id="judulCatatan">
                <p>Arsip Catatan Dosen</p>
            </div>
            
            <div class="mx-md-5 mb-sm-5">
                <div id="cariCatatanPerwalian" class="row mt-md-5 mb-md-4 ml-sm-5">
                    <div class="col-md-7 col-sm-12 px-sm-0 mb-sm-2">
                        <div class="mb-2 d-inline">
                            <span class="material-symbols-outlined search ">
                                search
                                </span>
                            <input id="inputCariMahasiswa"  class="mb-2" type="text" placeholder="Cari nama mahasiswa">
                        </div>
        
                        <div id="alertPencarian">
                            <p>Ditemukan 4 hasil sesuai kata pencarian</p>
                        </div>
                    </div>

                    <div id="opsiFilter" class="col-md-4 col-sm-12 px-0">
                        <div class="btn-group mb-3 mr-3">
                            <button type="button" class="btn dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            <span>Tahun : </span><span> {{tahunArsip}}  </span> 
                            </button>
                            
                            <div class="dropdown-menu">
                                <a class="dropdown-item" @click="tahunArsip=2020">2020</a>
                                <a class="dropdown-item" @click="tahunArsip=2021">2021</a>
                                <a class="dropdown-item" @click="tahunArsip=2022">2022</a>
                                <a class="dropdown-item" @click="tahunArsip=2023">2023</a>
                            </div>
                        </div>

                        <div class="btn-group mb-3">
                            <button type="button" class="btn dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            <span>Kategori : </span><span> {{tipeCatatan}}  </span> 
                            </button>
                            
                            <div class="dropdown-menu">
                                <a class="dropdown-item" @click="tipeCatatan='Refleksi Dosen',filter='Refleksi Dosen'">Relfeksi Dosen</a>
                                <a class="dropdown-item" @click="tipeCatatan='Khusus',filter='Khusus'">Khusus</a>
                            </div>
                        </div>
                    </div>                
                </div>

                <div id="listCatatanArsip" class="mb-md-5 mt-sm-4">
                    <div id="listCatatanArsipMahasiswa">
                        <div class="col-12" id="judulTipeCatatan">
                            <p class="mb-3 ml-5">Catatan Perwalian {{filter}}</p>
                        </div>
                        <div class="col-12">
                            <div class="list-group list-group-flush">
                                <div v-if="filter==='Refleksi Dosen'">
                                    <div v-for="(items,index) in listRefleksiDosen" :key="index" id="itemCatatanArsip" class="d-flex list-group-item list-group-item-action p-2">
                                        <div class="col-3 px-0 py-2">
                                            <div class="d-flex ml-5">
                                                <div class="d-flex align-items-center mr-3">
                                                    <span class="material-symbols-outlined">person_filled</span>
                                                </div>
        
                                                <div id="dataPertamaCatatan">
                                                    <p class="mb-0">{{items.namaMahasiswa}}</p>
                                                    <p class="mb-0">{{items.judul}}</p>
                                                </div>
                                            </div>
                                        </div>
        
                                        <div id="tanggalArsip" class="col-6 d-flex align-items-center">
                                            <p class="mb-0 mx-auto">
                                                <v-tooltip 
                                                    activator="parent"
                                                    location="bottom"
                                                    content-class="bg-grey-darken-1"
                                                    >Tanggal diarsipkan
                                                </v-tooltip>
                                                {{items.tanggalArsip}}
                                            </p>
                                        </div>
        
                                        <div id="opsiCatatan" class="col-3 d-flex align-items-center">
                                            <div class="ml-auto d-flex pr-5">
                                                <div class="mr-2">
                                                    <v-tooltip 
                                                        activator="parent"
                                                        location="bottom"
                                                        content-class="bg-grey-darken-1"
                                                        >Pulihkan Catatan
                                                    </v-tooltip>
                                                    <span class="material-symbols-outlined text-warning" @click="deleteCatatanMahasiswa(index)">
                                                        settings_backup_restore
                                                    </span>
                                                </div>
                                                <div>
                                                    <v-tooltip 
                                                        activator="parent"
                                                        location="bottom"
                                                        content-class="bg-grey-darken-1"
                                                        >Hapus Permanen Catatan
                                                    </v-tooltip>
                                                    <span class="material-symbols-outlined text-danger" @click="deleteCatatanMahasiswa(index)">
                                                        delete
                                                    </span>
                                                </div>
                                            </div>
                                            
                                        </div>
                                    </div>
                                </div>

                                <div v-else-if="filter==='Khusus'">
                                    <div v-for="(items,index) in listKhusus"  :key="index" id="itemCatatanArsip" class="p-2 d-flex list-group-item list-group-item-action">
                                        <div class="col-3 px-0">
                                            <div class="d-flex ml-3">
                                                <div class="d-flex align-items-center mr-3">
                                                    <span class="material-symbols-outlined">person_filled</span>
                                                </div>
        
                                                <div id="dataPertamaCatatan">
                                                    <p class="mb-0">{{items.namaMahasiswa}}</p>
                                                    <p class="mb-0">{{items.judul}}</p>
                                                </div>
                                            </div>
                                        </div>
        
                                        <div id="tanggalArsip" class="col-6 d-flex align-items-center">
                                            <p class="mb-0 mx-auto" data-toggle="tooltip" title="Tanggal diarsipkan">{{items.tanggalArsip}}</p>
                                        </div>
        
                                        <div id="opsiCatatan" class="col-3 d-flex align-items-center">
                                            <div class="ml-auto d-flex pr-5">
                                                <div class="mr-2">
                                                    <span class="material-symbols-outlined text-warning" data-toggle="tooltip" title="Pulihkan Catatan" @click="deleteCatatanOrangTua(index)">
                                                        settings_backup_restore
                                                    </span>
                                                </div>
                                                <div>
                                                    <span class="material-symbols-outlined text-danger" data-toggle="tooltip" title="Hapus Catatan" @click="deleteCatatanOrangTua(index)">
                                                        delete
                                                    </span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div> 
        </div>

    </div>
</template>

<script>

    class Catatan{
        constructor(namaMahasiswa,judul, tanggalArsip){
            this.namaMahasiswa = namaMahasiswa;
            this.judul = judul;
            this.tanggalArsip = tanggalArsip;
        }
    }

    import BreadcrumbModule from './BreadcrumbModule.vue';

    export default {
        name:"ArsipDosen",
        components:{
            BreadcrumbModule
        },
        data(){
            return{
                isOn:false,
                isOn2:false,
                indexHapusCatatan:0,
                tahunArsip:2019,
                tipeCatatan:"Refleksi Dosen",
                filter:"Refleksi Dosen",
                listRefleksiDosen:[
                    new Catatan("Nama Peserta 1", "Remindering UTS","14 Januari 2023"),
                    new Catatan("Nama Peserta 2", "Remindering UTS","14 Januari 2023"),
                    new Catatan("Nama Peserta 3", "Remindering UTS","14 Januari 2023"),
                    new Catatan("Nama Peserta 4", "Remindering UTS","14 Januari 2023"),
                    new Catatan("Nama Peserta 5", "Remindering UTS","14 Januari 2023"),
                    new Catatan("Nama Peserta 6", "Remindering UTS","14 Januari 2023"),
                    new Catatan("Nama Peserta 7", "Remindering UTS","14 Januari 2023"),
                    new Catatan("Nama Peserta 8", "Remindering UTS","14 Januari 2023"),
                ],
                listKhusus:[
                    new Catatan("Nama Peserta 9", "Konsultasi Beasiswa","14 Januari 2023"),
                    new Catatan("Nama Peserta 10", "Konsultasi Beasiswa","14 Januari 2023"),
                    new Catatan("Nama Peserta 11", "Konsultasi Beasiswa","14 Januari 2023"),
                    new Catatan("Nama Peserta 12", "Konsultasi Beasiswa","14 Januari 2023"),
                    new Catatan("Nama Peserta 13", "Konsultasi Beasiswa","14 Januari 2023"),
                    new Catatan("Nama Peserta 14", "Konsultasi Beasiswa","14 Januari 2023"),
                    new Catatan("Nama Peserta 15", "Konsultasi Beasiswa","14 Januari 2023"),
                    new Catatan("Nama Peserta 16", "Konsultasi Beasiswa","14 Januari 2023"),
                ]

            }
        
        },
        methods: {
            batal(){
                this.isOn = false;
            },
            simpan(){
                this.isOn = false;
            },
            hapusCatatanTidak(){
                this.isOn2 = false;
            },
            hapusCatatanYa(){
                this.deleteCatatan(this.indexHapusCatatan);
                this.isOn2 = false;
            },
            editCatatan(link){
                window.open(link);
            },
            deleteCatatanMahasiswa(index){
                this.listRefleksiDosen.splice(index,1);
            },
            deleteCatatanOrangTua(index){
                this.listRefleksiDosen.splice(index,1);
            },
            scrollTop(){
                window.scrollTo({
                    top: 0,
                    behavior: "smooth" // Animasi smooth scrolling
                });
            }
        },
        mounted(){
            import('bootstrap');
            import('bootstrap/dist/css/bootstrap.min.css');

             // mounted untuk bagian script src
            //slim.min.js
            const script1 = document.createElement("script");
            script1.src = "https://code.jquery.com/jquery-3.5.1.slim.min.js";
            script1.integrity =
            "sha384-DfXdz2htPH0lsSSs5nCTpuj/zy4C+OGpamoFVy38MVBnE+IbbVYUew+OrCXaRkfj";
            script1.crossOrigin = "anonymous";
            document.head.appendChild(script1);

            // //popper.min.js
            const script2 = document.createElement("script");
            script2.src ="https://cdn.jsdelivr.net/npm/popper.js@1.12.9/dist/umd/popper.min.js";
            script2.integrity = "sha384-hSM2mzKd06KfNmOz6mK6+sfuLdYVjI1MKOpnE+O+hNEZmZ+zQp8hJz3uPL2twNJX";
            script2.crossOrigin = "anonymous";
            document.head.appendChild(script2);

            //bundle.min.js
            const script3 = document.createElement("script");
            script3.src ="https://cdn.jsdelivr.net/npm/bootstrap@4.5.3/dist/js/bootstrap.bundle.min.js";
            script3.integrity = "sha384-ho+j7jyWK8fNQe+A12Hb8AhRq26LrZ/JpcUGGOn+Y7RsweNrtN/tE3MoK7ZeZDyx";
            script3.crossOrigin = "anonymous";
            document.head.appendChild(script3);
        },
        created(){
            this.scrollTop();
        }

    }
</script>
<style scoped src="../../public/frontend/styling/arsipPerwalian.css">

</style>