                                        </div>
        
                                        <div id="opsiCatatan" class="col-3 d-flex align-items-center">
                                            <div class="ml-auto d-flex pr-5">
                                                <div class="mr-2">
                                                    <span class="material-symbols-outlined text-warning" data-toggle="tooltip" title="Pulihkan Catatan" @click="deleteCatatanMahasiswa(index)">
                                                        settings_backup_restore
                                                    </span>
                                                </div>
                                                <div>
                                                    <span class="material-symbols-outlined text-danger" data-toggle="tooltip" title="Hapus Catatan" @click="deleteCatatanMahasiswa(index)">
                                                        delete
                                                    </span>
                                                </div>
                                            </div>
                                            
                                        </div>
                                    </div>
                                </div>

                                <div v-else-if="filter==='Grup Angkatan'">
                                    <div v-for="(items,index) in listGrupAngkatan"  :key="index" id="itemCatatanArsip" class="p-2 d-flex list-group-item list-group-item-action">
                                        <div class="col-3 px-0 py-2">
                                            <div class="d-flex ml-5">
                                                <div class="d-flex align-items-center mr-3">
                                                    <span class="material-symbols-outlined">person_filled</span>
                                                </div>
        
                                                <div id="dataPertamaCatatan">
                                                    <p class="mb-0">{{items.namaMahasiswa}}</p>
                                                    <p class="mb-0">{{items.judul}}</p>
                                                </div>
                                            </div>
                                        </div>
        
                                        <div id="tanggalArsip" class="col-6 d-flex align-items-center">
