                                                <v-tooltip 
                                                    activator="parent"
                                                    location="bottom"
                                                    content-class="bg-grey-darken-1"
                                                    >Tanggal diarsipkan
                                                </v-tooltip>
                                                {{items.tanggalArsip}}
                                            </p>
                                        </div>
        
                                        <div id="opsiCatatan" class="col-3 d-flex align-items-center">
                                            <div class="ml-auto d-flex pr-5">
                                                <div class="mr-2">
                                                    <span class="material-symbols-outlined text-warning"  @click="deleteCatatanOrangTua(index)">
                                                        <v-tooltip 
                                                            activator="parent"
                                                            location="bottom"
                                                            content-class="bg-grey-darken-1"
                                                            >Pulihkan Catatan
                                                        </v-tooltip>
                                                        settings_backup_restore
                                                    </span>
                                                </div>
                                                <div>
                                                    <span class="material-symbols-outlined text-danger"  @click="deleteCatatanOrangTua(index)">
                                                        <v-tooltip 
                                                            activator="parent"
                                                            location="bottom"
                                                            content-class="bg-grey-darken-1"
                                                            >Hapus Catatan
                                                        </v-tooltip>
                                                        delete
                                                    </span>
                                                </div>
                                            </div>
                                            
                                        </div>
                                    </div>
                                </div>
                                
                            
