<script>
 import BreadcrumbModule from './BreadcrumbModule.vue';
    class Catatan{
        constructor(namaMahasiswa,judul, tanggalArsip){
            this.namaMahasiswa = namaMahasiswa;
            this.judul = judul;
            this.tanggalArsip = tanggalArsip;
        }
    }

    // $(function(){
    //     $('[data-toggle="tooltip"]').tooltip();   
    // });

    export default {
        name:"ArsipPerwalianGrupAngkatan",
        data(){
            return{
                isOn:false,
                isOn2:false,
                indexHapusCatatan:0,
                tahunArsip:2019,
                tipeCatatan:"Grup Angkatan",
                filter:"Grup Angkatan",
                listGrupAngkatan:[
                    new Catatan("Angkatan 2019", "Perwalian Pra UTS","14 Januari 2023"),
                    new Catatan("Angkatan 2019", "Perwalian Pra UTS","14 Januari 2023"),
                    new Catatan("Angkatan 2019", "Perwalian Pra UTS","14 Januari 2023"),
                    new Catatan("Angkatan 2019", "Perwalian Pra UTS","14 Januari 2023"),
                    new Catatan("Angkatan 2019", "Perwalian Pra UTS","14 Januari 2023"),
                    new Catatan("Angkatan 2019", "Perwalian Pra UTS","14 Januari 2023"),
                    new Catatan("Angkatan 2019", "Perwalian Pra UTS","14 Januari 2023"),
                    new Catatan("Angkatan 2019", "Perwalian Pra UTS","14 Januari 2023"),
                ],
            }
        },
        components:{
            BreadcrumbModule
        },
        methods: {
            batal(){
                this.isOn = false;
            },
            simpan(){
                this.isOn = false;
            },
            hapusCatatanTidak(){
                this.isOn2 = false;
            },
            hapusCatatanYa(){
                this.deleteCatatan(this.indexHapusCatatan);
                this.isOn2 = false;
            },
            editCatatan(link){
                window.open(link);
            },
            deleteCatatanMahasiswa(index){
                this.listGrupAngkatan.splice(index,1);
            },
            deleteCatatanOrangTua(index){
                this.listGrupAngkatan.splice(index,1);
            },
            bukaArsip(){
                window.open("#");
            },
            scrollTop(){
                window.scrollTo({
                    top: 0,
                    behavior: "smooth" // Animasi smooth scrolling
                });
            }

        
        },
        mounted(){
            import('bootstrap');
            import('bootstrap/dist/css/bootstrap.min.css');

             // mounted untuk bagian script src
            //slim.min.js
            const script1 = document.createElement("script");
            script1.src = "https://code.jquery.com/jquery-3.5.1.slim.min.js";
            script1.integrity =
            "sha384-DfXdz2htPH0lsSSs5nCTpuj/zy4C+OGpamoFVy38MVBnE+IbbVYUew+OrCXaRkfj";
            script1.crossOrigin = "anonymous";
            document.head.appendChild(script1);

            //popper.min.js
            const script2 = document.createElement("script");
            script2.src ="https://cdn.jsdelivr.net/npm/popper.js@1.12.9/dist/umd/popper.min.js";
            script2.integrity = "sha384-hSM2mzKd06KfNmOz6mK6+sfuLdYVjI1MKOpnE+O+hNEZmZ+zQp8hJz3uPL2twNJX";
            script2.crossOrigin = "anonymous";
            document.head.appendChild(script2);

            //bundle.min.js
            const script3 = document.createElement("script");
            script3.src ="https://cdn.jsdelivr.net/npm/bootstrap@4.5.3/dist/js/bootstrap.bundle.min.js";
            script3.integrity = "sha384-ho+j7jyWK8fNQe+A12Hb8AhRq26LrZ/JpcUGGOn+Y7RsweNrtN/tE3MoK7ZeZDyx";
            script3.crossOrigin = "anonymous";
            document.head.appendChild(script3);
        },
        created(){
            this.scrollTop();
        }

    }
</script>
<style scoped src="../../public/frontend/styling/arsipPerwalian.css">

